import React from 'react';
import { Link } from 'react-router-dom';
import "../CartEmpty/CartEmpty.scss";
import cart from "../../../img/empty_cart.jpg";
import { PropTypes } from 'prop-types';

const CartEmpty = ({ setUnactiveCart }) => {
    return (
        <div className="empty">
            <h2 className="empty__title">  :&#40; </h2>
            <p className="empty__text">        </p>
            <Link onClick={() => { setUnactiveCart(false) }} className="empty__link" to="/catalog"></Link>
            <img src={cart} alt="(  )" className="empty__image" />
        </div>
    );
};

CartEmpty.propTypes = {
    setUnactiveCart: PropTypes.func.isRequired
}

export default CartEmpty;