import React from 'react';
import { useForm } from "react-hook-form";
import arrowRightWebp from '../../../img/arrow-right.webp';
import arrowRightPng from '../../../img/arrow-right.png';

const FooterForm = () => {
    const { register, handleSubmit, formState: { errors, isValid }, reset } = useForm({ mode: "onBlur" });
    const onSubmit = data => {
        console.log(data);
        reset();
    };
    return (
        <form onSubmit={handleSubmit(onSubmit)} className="footer__form subform">
            <div className="subform__title title">  </div>
            <div className="subform__text">      ?     
                
                 </div>
            <div className="subform__searchblock">
                <input
                    {...register("Telephone", {
                        required: "  ",
                        pattern: {
                            value: /^[\+]?[(]?[0-9]{3}[)]?[-\s\.]?[0-9]{3}[-\s\.]?[0-9]{4,6}$/im,
                            message: "   !!"
                        }
                    })}
                    type="text" placeholder=" " className="subform__input" />
                {errors?.Telephone && <p className="form__error">{errors?.Telephone?.message || "!"}</p>} <label htmlFor="before__footer">&nbsp;</label>
                <button disabled={!isValid} className="subform__btn">
                    <picture><source srcSet={arrowRightWebp} type="image/webp" /><img src={arrowRightPng} alt="  (  )" id="before__footer" /></picture>
                </button>
            </div>
        </form>
    );
};

export default FooterForm;